.box {
  height: 100%;
  font-size: 30px;
  font-weight: bold;
  margin: auto;
  display: flex;
  justify-content: center;
}

.comment {
  align-self: center;
  margin: 0;
  color: #8383ab;
}

.comment:hover {
  color: #3c3c59;
}

.l-header {
  border-radius: 5px;
  width: 100px;
  height: 100px;
  background-color: pink;
  width: 100%;
}

.l-footer {
  display: flex;
  justify-content: center;
  height: 100px;
}

.copyright {
  align-self: center;
  color: pink;
}

.copyright:hover {
  color: #ffdae0;
}

* {
  margin: 0;
  padding: 0;
}

.main__section {
  background-color: #b7b5b5;
  height: 300px;
  text-align: center;
}

.main__section:first-of-type {
  background-color: #e27788;
}

.main__section:last-of-type {
  background-color: pink;
}

.main__section--Title {
  font-size: 30px;
  color: white;
  padding: 20px;
}

.main__section--contents {
  display: flex;
  justify-content: space-around;
  font-size: 20px;
  color: white;
}

.main__section--items {
  font-weight: normal;
  list-style: none;
}
